.item_movie{
  display:block;
  position:relative;
  height:225px;
  border-radius:10px;
  background:#fff;
  transition:box-shadow 0.3s;
}
.item_movie:hover{
  box-shadow:0 2px 15px 0 rgba(0,0,0,0.2);
}
.item_movie:after{
  display:block;
  clear:both;
  content:'';
}
.item_movie .rank{
  position:absolute;
  top:-32px;
  right:15px;
  font-weight:900;
  font-size:40px;
  color:#e63b60;
  text-shadow:2px 2px #ffd3de;
  transition:color 0.3s;
}
.item_movie:hover .rank{
  color:#151a7b;
  text-shadow:2px 2px #c5c8ff;
}
.thumb_movie{
  float:left;
  overflow:hidden;
  width:150px;
  height:100%;
  border-radius:10px 0 0 10px;
}
.thumb_movie img{
  height:100%;
}
.cont_movie{
  overflow:hidden;
  padding:25px 20px 15px;
}
.cont_movie .tit_movie{
  overflow:hidden;
  font-size:20px;
  white-space:nowrap;
  text-overflow:ellipsis;
}
.cont_movie .info_movie{
  padding-top:4px;
}
.cont_movie .info_movie dd{
  display:inline-block;
  font-size:14px;
  vertical-align:top;
}
.cont_movie .info_movie dd ~ dd:before{
  display:inline-block;
  width:1px;
  height:12px;
  margin:5px 7px 0;
  vertical-align:top;
  background:#aaa;
  content:'';
}
.cont_movie .desc_movie{
  padding-top:10px;
  font-size:14px;
  color:#333
}